<template>
  <div>
    <el-card>
      <template #header>
        <div class="card-header">
          <span>数据列表</span>
          <export-excel file-name="数据" :header="tableData.map(item=>item.label)"
              :filter-filed="tableData.map(item=>item.value)" :data="showTableData">导出</export-excel>
        </div>
      </template>
      <el-table
        :data="
          showTableData.slice(
            (currentPage - 1) * pagesize,
            currentPage * pagesize
          )
        "
        border
        class="table"
        row-key="id"
        ref="multipleTable"
        header-cell-class-name="table-header"
        :current-page="currentPage"
        @selection-change="handleSelectionChange"
        stripe
      >
        <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
        <el-table-column
          v-for="i in tableData"
          :key="i.value"
          :prop="i.value"
          :label="i.label"
        ></el-table-column>
      </el-table>
      <div class="pagination ">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[2, 5, 10]"
          :page-size="pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
import ExportExcel from '../pages/excel/ExportExcel'

export default {
  name: "easyTable",
  props: ["tableData", "showTableData"],
  data() {
    return {
      index:[],
      currentPage: 1, //当前页
      pagesize: 5, //每页数量
      total: 0, //
      multipleSelection:'',
      boolExp:true,
      exportData:[]
    };
  },
  mounted() {
    this.total = this.showTableData.length;
  },
  methods: {
    handleSizeChange(val) {
      this.pagesize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },
    // 多选操作
    handleSelectionChange(val) {
      console.log(val)
      this.index=[]
      this.multipleSelection = val;
      for(let i=0;i<this.multipleSelection.length;i++){
        this.index.push(this.multipleSelection)
      }
    },

  },
  watch:{
    multipleSelection(val){
      if(val.length){
        this.boolExp=false
      }else this.boolExp=true
    }
  },
  computed:{
    // exportTable(){
    //   if(this.index.length){
    //     return this.index
    //   } else return this.showTableData
    // }
  },
  components:{
    ExportExcel
  }
};
</script>

<style scoped>
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.table {
  width: 100%;
  font-size: 14px;
}

.table-td-thumb {
  display: block;
  margin: auto;
  width: 40px;
  height: 40px;
}
</style>
